.todoListMain .header input {
    padding: 10px;
    font-size: 16px;
    border: 2px solid #FFF;
    width: 225px;
    border-radius: 5px;
}

#timeSelector {
    padding: 10px;
    font-size: 16px;
    font-family: sans-serif;
    border: 2px solid #FFF;
    border-radius: 5px;
    width: 225px;
}

#timeLabel {
    padding: 10px;
}

.todoListMain .header button {
    padding: 10px;
    font-size: 16px;
    margin-top: 10px;
    background-color: #0066FF;
    color: #FFF;
    border-radius: 5px;
    width: 250px;
    border: 2px solid #0066FF;
}

.todoListMain .header button:hover {
    background-color: #003399;
    border: 2px solid #003399;
    cursor: pointer;
}

.todoListMain .theList {
    list-style: none;
    padding-left: 0;
    width: 250px;
}

.todoListMain .theList li {
    color: #333;
    background-color: rgba(255,255,255,.5);
    padding: 15px;
    margin-bottom: 15px;
    border-radius: 5px;
    word-wrap: break-word;
    transition: rgba(255,255,255,.5) .2s ease-out;
}

.todoListMain .theList li:hover {
    background-color: pink;
    cursor: pointer;
}

ul.theList {
    padding: 0;
}

.timeCountdown {
    float: right;
}